<template>
    <div id="python">
      <div class="home-title">
        <h2><Icon type="ios-book" />Python文章</h2>
      </div>
      <div class="showdata">
        <article-list :param="toChild"></article-list>
      </div>
    </div>
</template>

<script>
  import articleList from './articleList';
    export default {
      name: "python",
      data(){
        return{
          toChild:{
            login:this.outdata, //来自父组件
            flag: "python",
          },
        }
      },
      components: {articleList},
      props:['outdata'],
    }
</script>

<style scoped lang="less">
  /*start less类*/
  .h2(){
    h2{
      border-bottom: 1px solid rgba(83, 83, 83, 0.15);
    }
    h2::after{
      content: "";
      display: block;
      width: 18%;
      border: 1px solid rgb(0, 162, 255);
      vertical-align: middle;
    }
  }
  /*end less类*/
  @media (max-width: 750px){
    #python{
      width: 90%;
      margin: 0 auto;
      .home-title{
        text-align: left;
        .h2();
      }
    }
  }
  @media (min-width: 750px){
    #python{
      .home-title{
        text-align: left;
        .h2();
      }
    }
  }
</style>



